import React, { Component } from 'react';
import './App.css';
import { BrowserRouter as Router, Route, Redirect } from 'react-router-dom';
import axios from 'axios';

import SignIn from "./components/SignIn";
import SignUp from "./components/SignUp";
import Home from "./components/Home";
import HomePage from "./pages/HomePage";
import Admin from "./pages/AdminHome";
import CurrentProjects from "./pages/CurrentProjects";
import CompletedFutureProjects from "./pages/CandFProjects";
import PatternsInStock from "./pages/PatternsInStock";
import PatternsDW from "./pages/PatternsDandW";
import FabricsInStock from "./pages/FabricsInStock";
import FabricsHalfOrLess from "./pages/FabricsHalforLess";

class App extends Component {
  state = {
    username: "",
    password: "",
    auth: {
      userId: "",
      username: "",
      isAuthenticated: false
    }
  };

  componentWillMount() {
    axios.get("/auth/isAuthenticated").then((result) => {
      const { userId, isAuthenticated, username } = result.data
      this.setState({
        auth: {
          userId,
          isAuthenticated,
          username
        }
      });
    });
  }

  handleChange = (event) => {
    const { name, value } = event.target;
    // Set the state for the appropriate input field
    this.setState({
      [name]: value
    });
  }

  handleSubmit = (event) => {
    event.preventDefault();

    //call a sign In function
    const newUser = {
      username: this.state.username,
      password: this.state.password
    };
    this.setState({
      username: "",
      password: ""
    });
    const { name } = event.target;
    axios.post(name, newUser).then((data) => {
      if (data.data.isAuthenticated) {
        const { userId, isAuthenticated, username } = data.data;
        this.setState({
          auth: {
            userId,
            isAuthenticated,
            username
          }
        });
      }
    });
  }

  handleLogout = (event) => {
    event.preventDefault();
    axios.get("/auth/logout").then((result) => {
      this.setState({
        auth: {
          userId: "",
          username: "",
          isAuthenticated: false
        }
      });
    })
  };

  render() {
    const loggedIn = this.state.auth.isAuthenticated;
    return (
      <Router>
        <div>          
          <Route exact path="/" render={() => {
            if (loggedIn) {
              return <Redirect to="/adminhome" />
            } else {
              return <SignIn
                handleChange={this.handleChange}
                handleSubmit={this.handleSubmit}
                email={this.state.email}
                password={this.state.password}
              />
            }
          }} />
          <Route exact path="/signup" render={() => {
            if (loggedIn) {
              return <Redirect to="/adminhome" />
            } else {
              return <SignUp
                handleChange={this.handleChange}
                handleSubmit={this.handleSubmit}
                email={this.state.email}
                password={this.state.password}
              />
            }
          }} />
         <Route exact path="/home" component={HomePage} render={() => {
            if (!loggedIn) {
              return <Redirect to="/adminhome" />
            } else {
              return <Home handleLogout={this.handleLogout} auth={this.state.auth} />
            }
          }
          } />           
          <Route exact path="/adminhome" render={()=>{
            if (!loggedIn) {
              return <Redirect to="/" />
            } else {
              return <Admin handleLogout={this.handleLogout} auth={this.state.auth}/>
            }           
        }} />
          
          <Route exact path="/current-projects" render={()=>{
            if (!loggedIn) {
              return <Redirect to="/" />
            } else {
              return <CurrentProjects handleLogout={this.handleLogout} auth={this.state.auth}/>
            }   
          }} />
          
          <Route exact path="/completedfuture" render={()=>{
            if (!loggedIn) {
              return <Redirect to="/" />
            } else {
              return <CompletedFutureProjects handleLogout={this.handleLogout} auth={this.state.auth}/>
            }   
          }} />
          <Route exact path="/patterns-in-stock" render={()=>{
            if (!loggedIn) {
              return <Redirect to="/" />
            } else {
              return <PatternsInStock handleLogout={this.handleLogout} auth={this.state.auth}/>
            }   
          }} />
          <Route exact path="/damagedwishlist-patterns" render={()=>{
            if (!loggedIn) {
              return <Redirect to="/" />
            } else {
              return <PatternsDW handleLogout={this.handleLogout} auth={this.state.auth}/>
            }   
          }} />
          <Route exact path="/fabrics-in-stock" render={()=>{
            if (!loggedIn) {
              return <Redirect to="/" />
            } else {
              return <FabricsInStock handleLogout={this.handleLogout} auth={this.state.auth}/>
            }   
          }} />
          <Route exact path="/fabricshalfyardunder" render={()=>{
            if (!loggedIn) {
              return <Redirect to="/" />
            } else {
              return <FabricsHalfOrLess handleLogout={this.handleLogout} auth={this.state.auth}/>
            }   
          }} />
        </div>
      </Router>

    );
  }
}

export default App;


